import React from 'react'
import { AppRegistry, StyleSheet } from 'react-native'
import {ScrollContainer, Container, ActionBar} from '../peppercorn'


// Components
const Example = () => (
  <Container>
    <ActionBar title="ActionBar" stable header/>
    <ScrollContainer style={{backgroundColor: '#DCDCDC'}}>
      <ActionBar title="My Second Header" primary/>
    </ScrollContainer>

    <ActionBar title="Footer" footer dark/>
  </Container>
);

// App registration and rendering
AppRegistry.registerComponent('ex', () => Example)
AppRegistry.runApplication('ex', { rootTag: document.getElementById('react-root') })



